.Navbar {
  margin-bottom: 1rem;
  padding: 3rem;
  width: 100%;
  height: 3.7rem;
  position: sticky;
  top: 0;
  z-index: 1;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
}
.navContainer {
  width: 90%;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
